  .menu {
    display: none;
  }
}

.contenedor-menu {
  animation-duration: 1s;
  animation-name: desplazar;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .contenedor-menu {
    display: none;
  }
}
.contenedor-menu a {
  text-align: center;
  display: block;
  color: #FFFFFF;
  padding: 2rem;
  background-color: #f96ef9;
  margin: 0.5rem 0;
  transition: background-color, color 0.4s ease-in-out;
}
.contenedor-menu a:hover {
  cursor: pointer;
  background-color: #c55cd6;
  font-weight: bold;
}
.contenedor-menu a i {
  margin-right: 0.8rem;
}

@keyframes desplazar {
  from {
    margin-left: -100%;
  }
  to {
    margin-left: 0%;
  }
}
