.alerta.exito {
  background-color: #0de018;
  margin-top: 2rem;
}

.w-100 {
  width: 100%;
}

.lista {
  width: 100%;
  margin-top: 2rem;
  border-spacing: 0;
}
.lista thead {
  background-color: #a52eb8;
}
.lista th {
  color: #FFFFFF;
  padding: 1rem 0;
}
.lista img {
  margin: 0 auto;
  width: 15rem;
}
.lista a {
  margin: 0;
  margin-bottom: 1rem;
  padding: 1rem;
  display: block;
}
.lista td {
  text-align: center;
  color: #000;
}

.menuAdmin__titulo {
  text-align: center;
  margin: 3rem 0;
  color: #a52eb8;
}
@media (min-width: 768px) {
  .menuAdmin__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.menuAdmin a:nth-of-type(1) {
  background-color: #ff6961;
}
.menuAdmin a:nth-of-type(2) {
  background-color: #77dd77;
}
.menuAdmin a:nth-of-type(3) {
  background-color: #fcb7af;
}
.menuAdmin a:nth-of-type(4) {
  background-color: #84b6f4;
}
.menuAdmin a:nth-of-type(5) {
  background-color: #fdcae1;
}
.menuAdmin a:nth-of-type(6) {
  background-color: #fdfd96;
}
.menuAdmin a:hover {
  cursor: pointer;
}

.submenu {
  display: grid;
  place-items: center;
  padding: 3rem 2rem;
}
.submenu p {
  font-weight: bold;
  margin: 0;
}
.submenu i {
  display: inline-block;
  font-size: 3.5rem;
  margin-bottom: 1rem;
}